export default {
	data() {
		return {
			merchantId: '',
			user_name:'',
			user_img:'',
			gross_income:'',
			today_income:'',
			today_order:'',
			
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.getinfo();
				that.getbalance();
				that.getToday_income();
			}
		});
	},
	methods: {
		getinfo: function() {
			var that = this;
			uni.request({
				url: that.serveipd+"/api/merchant/auth/info/"+that.merchantId,
				method: 'GET',
				success: res => {
				let infor=res.data.data;
				that.user_name=infor.name;
				that.user_img=infor.shopTrademarkUrl;
				},
			});
		},
		getbalance: function() {
			var that = this;
			uni.request({ 
				url: that.serveipd + "/api/merchant/money/balance",
				method: 'GET',
				data: {
					merchantId: that.merchantId
				},
				success: res => {
				that.gross_income=res.data.data;
				
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getToday_income:function(){
			let that=this;
			uni.request({
				url:this.serveipd+"/api/merchant/money/day",
				method: 'GET',
				data: {
					merchantId:that.merchantId
				},
				success: res => {
					console.log(res);
						that.today_income=res.data.data.amount.toString();
						that.today_order=res.data.data.nums.toString();
				},
			});
		},
		gostore: function() {
			uni.navigateTo({
				url: '/pages/user/store',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		email_fuc: function() {
			uni.navigateTo({
				url: '/pages/user/msgcenter',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
}
